.page {
  font-family: Manrope, sans-serif;
  color: #1b2129;
  scroll-behavior: smooth;

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.7s;
    opacity: 0;

    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      position: fixed;
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &__body {
    min-width: 320px;
    margin: 0;
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
